/* Custom Properties */
@property --a { 
	syntax: '<angle>';
	initial-value: 0deg;
	/* used only on pseudo, nowhere to be inherited, 
	 * better perf if set false, see 
	 * https://www.bram.us/2024/10/03/benchmarking-the-performance-of-css-property/ */
	inherits: false
}

/* General body and html settings */
html, body, #root {
  font-family: system-ui;
  width: 100vw;
  min-height: 100vh; /* Ensures the full height is used without overflow */
  overflow: hidden;
  margin: 0;
  padding: 0;
  box-sizing: border-box;

  @media (max-width: 768px) {
    height: auto; /* Allows content to adapt on mobile */
  }
}

/* Avoid margin collapse */
*, *::before, *::after {
  box-sizing: inherit;
  background-color: transparent;
}

button {
  background-color: transparent;
  border: none;
}

.homeLogo{
  width: 20rem;
  height: 8rem;
  position: relative;

}

.signInOptions {
  gap: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 60dvh;
  margin-bottom: 20dvh;
  @media (max-width: 768px) {
    flex-direction: column;
    align-items: center;
    margin-top: 0;
 }
}

.emailInputs {

  display: flex;
  flex-direction: column;
  justify-content: center;

}

.emailSignIn {
  color: white;
  height: 3rem;
  width: 15rem;
  font-size: 2rem;
}

.passwordSignIn {
  color: white;
  height: 3rem;
  width: 15rem;
  font-size: 2rem;
}

.signUpWithEmail, .signInWithEmail {
  height: 2rem;
  width: 15rem;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px 20px;
  font-size: 1.25rem;
  font-weight: bold;
  color: #000000;
  background: linear-gradient(90deg, #34a853, #4285f4, #ea4335, #fbbc05);
  background-size: 300%;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-position 0.4s ease, transform 0.3s ease, box-shadow 0.3s ease;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.signUpWithEmail:hover, .signInWithEmail:hover {
  background-position: 100%;
  transform: scale(1.05);
  box-shadow: 0 8px 16px rgba(52, 168, 83, 0.4);
}

.signUpWithEmail:active, .signInWithEmail:active {
  transform: scale(1);
  box-shadow: 0 6px 12px rgba(52, 168, 83, 0.3);
}

.App {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
}

.App-header {
  color: white;
  display: flex;
  align-items: center;
  width: 100%;
  height: 5rem;
  @media (max-width: 768px) {
    height: 3rem; /* Adjust height for mobile to make more room */
    /* Adjust height for mobile to make more room */
 }
}

.navbar {
  background: linear-gradient(90deg, #a82428, #5d0609, #a82428);
  background-size: 200%;
  animation: shimmer 13s linear infinite;
  color: white;
  padding: 1rem;
  text-align: center;
  font-size: 1.5rem;
  overflow: hidden;
  color: white;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 100%;
  height: 100%;
}

@keyframes shimmer {
  0% {
    background-position: 0% 50%;
  }
  100% {
    background-position: 200% 50%;
  }
}


.dailyLogin {
  height: 4rem;
  width: 4rem;
  transition: transform 0.5s ease; /* Smooth transition for growth */
  @media (max-width: 768px) {
    /* Adjust position for mobile to make more room */
    height: 3rem;
  }
}

.dailyLogin:hover {
  transform: scale(1.05); /* Slightly grow the element */
  animation: glow 2s infinite alternate; /* Apply glow animation */
}

.discordLink {
    right: 22rem;
}

@keyframes glow {
  0% {
    background-color: #ff8ab8; /* Initial color */
  }
  50% {
    background-color: #ff004f; /* Darker or brighter red/pink for glow */
  }
  100% {
    background-color: #ff8ab8; /* Darker or brighter red/pink for glow */
  }
}

@keyframes textGlow {
  0% {
    text-shadow: 
      0 0 5px #ff8ab8,  /* Light pink */
      0 0 10px #ff7aa8, /* Slightly darker pink */
      0 0 20px #ff6a98, /* Mid pink */
      0 0 30px #ff5a88, /* Darker pink */
      0 0 40px #ff4a78; /* Deep pink */
  }
  50% {
    text-shadow: 
      0 0 5px #ffcbe0,  /* Lightest pink */
      0 0 15px #ffa8c5, /* Soft pink */
      0 0 25px #ff85aa, /* Brighter pink */
      0 0 35px #ff628f, /* Vibrant pink */
      0 0 45px #ff3f74; /* Deep pink */
  }
  100% {
    text-shadow: 
      0 0 5px #ff8ab8,  /* Light pink */
      0 0 10px #ff7aa8, /* Slightly darker pink */
      0 0 20px #ff6a98, /* Mid pink */
      0 0 30px #ff5a88, /* Darker pink */
      0 0 40px #ff4a78; /* Deep pink */
  }
}




.main-content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
  width: 100%;
  height: 100%;
  background-color: rgb(181, 56, 87);
  color: white;
  @media (max-width: 768px) {
    /* Adjust height for mobile to make more room */
    height: auto;
  }
}

.character-room-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: auto;
  background-color: rgb(71, 18, 26);
  color: white;
}

.character-room {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 2dvw;
  padding-top: 5dvh;
  padding-bottom: 5dvh;
  padding-left: 4dvw;
  padding-right: 4dvw;
  width: 90dvw;
  gap: 4dvw;
  overflow-y: auto;
  scrollbar-width: none;
  min-height: 90dvh;
  max-height: 90dvh; /* Allows it to scroll within a constrained area */
  @media (max-width: 768px) {
    height: auto; /* Adjust height for mobile to make more room */
    scrollbar-width: none;
    justify-content: space-between;
    padding: 1rem 2rem 5rem 2rem;
  }
}

.character-filters {
  z-index: 2;
  /* hide outer part of glow */
	overflow: hidden;
	/* needed for absolutely positioned pseudo */
	position: relative;
	/* adjust width as needed IF it's even necessary to set */
	/* adjust aspect-ratio OR height IF height not given by content */
	aspect-ratio: 1;
	/* round outer card corners */
	border-radius: .5em;
  height: 5rem;
  width: 50rem;
	
	/* text & layout styles below just for prettifying */
	place-self: center;
	place-content: center;
	padding: .5em;
	color: #ededed;
	font: clamp(1em, 2vw + 2vh, 2em) sans-serif;
	text-align: center;
	text-transform: uppercase;
  @media (max-width: 900px) {
    display: none;   
  }
 
}




.character-filters-dropdown {
  display: none;
  color: black;
  @media (max-width: 900px) {
    display: flex;
    
  }
}

.character-filters > button {
  color: #ff8ab8;
  font-size: 1.5rem;
  text-shadow: black 1px 1px 2px;
  font-weight: 600;
}

.character-filters > button:hover {
  color: #d02167;
  animation: textGlow 2s infinite alternate; /* Apply glow animation */
  font-weight: 600;
}


/* Vixens */

@keyframes rainbowGlow {
  0% {
    box-shadow: 0 0 1px 1px #ff4d4d, 0 0 2px 1px #ff6666, 0 0 3px 2px #ff8080, 0 0 4px 2px #ff99aa, 0 0 5px 3px #ffb3c1, 0 0 6px 3px #ffccd9, 0 0 7px 4px #ffe6f2;
    border-radius: 1rem;
  }
  50% {
    box-shadow: 0 0 1px 1px #ffe6f2, 0 0 2px 1px #ffccd9, 0 0 3px 2px #ffb3c1, 0 0 4px 2px #ff99aa, 0 0 5px 3px #ff8080, 0 0 6px 3px #ff6666, 0 0 7px 4px #ff4d4d;
    border-radius: 1rem;
  }
  100% {
    box-shadow: 0 0 1px 1px #ff4d4d, 0 0 2px 1px #ff6666, 0 0 3px 2px #ff8080, 0 0 4px 2px #ff99aa, 0 0 5px 3px #ffb3c1, 0 0 6px 3px #ffccd9, 0 0 7px 4px #ffe6f2;
    border-radius: 1rem;
  }
}



.vixen-card {
  width: 20dvw;
  height: 40dvh;
  border-radius: 10px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  transition: transform 0.2s, box-shadow 0.2s;
  @media (max-width: 768px) {
    height: 40dvh;
    width: 30dvw;
    font-size: 0.65rem;
    height: auto; /* Allows content to adapt on mobile */
  }
}

.vixen-card:hover {
  transform: scale(1.05);
}

.vixen-profile-card {
  width: 20dvw;
  height: 30dvh;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: rgb(145, 0, 0);
  box-shadow: inset 0 10px 25px rgba(0, 0, 0, 0.429);
  @media (max-width: 768px) {
    width: 30dvw;
    height: 30dvh;
    font-size: 0.65rem;
  }
}

.vixen-profile-card.shimmer {
  animation: rainbowGlow 3s linear infinite;
}



.vixen-name {
  font-weight: 700;
  font-size: 1rem;
  text-shadow: black 1px 1px 2px;
  cursor: default;
}






/* Consent Modal */
.consent-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.consent-content {
  background: #fff;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
}

.consent-content h2 {
  margin-bottom: 10px;
}

.consent-content button {
  margin: 5px;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}


.vixenSettingsButton {
  position: absolute;
  font-size: 1.5rem;
  width: 3rem;
  left: 0;
}