.titleBar {
	background-color: rgb(145, 35, 51);
	color: white;
	display: flex;
	align-items: center;
	width: 100%;
	height: 8rem;
	text-align: center;
	justify-content: space-between;
	padding: 0.5rem;
	box-shadow: 0 4px 14px rgba(0, 0, 0, 0.669);
  }
  
  .titlePic {
	height: 7rem;
	width: 7rem;
	border-radius: 50%;
	margin-right: 1%;
	margin-left: 5%;
	@media (max-width: 430px) {
		height: 4rem;
		width: 4rem;
	}
  }

  .chatTitle {
	font-size: 3rem;
	font-weight: 500;
	color: white;
	text-shadow: 1px 1px 2px black;
	@media (max-width: 430px) {
		font-size: 1.5rem;
	}
  }