/* Chat Rooms */

.chatrooms {
	flex-grow: 1;
	display: flex;
	flex-direction: column;
	width: 100%;
	min-height: 100dvh;
  }
  
  /* Chatroom */
  .chatBox {
	display: flex;
	flex-direction: column;
	width: 100%;
	height: 95dvh; /* Adjusted for navbar and chatBar */
	align-items: center;
  
	@media (max-width: 768px) {
	  height: calc(100dvh - 12dvh); /* Adjusted for mobile */
	}
  }
  
 
  
  .chatSettings {
	width: 4rem;
	height: 4rem;
	font-size: 3rem;
  }
  

  
  
  .chatMessages {
	flex-grow: 1;
	overflow-y: auto;
	padding: 1rem;
	padding-bottom: 10dvh;
	height: 100dvh;
	max-height: auto;
	scrollbar-width: none;
	scroll-behavior: smooth;
  }
  
  
  
  
  
  /* Chat room messaging */
  
  i {
	color:rgb(16, 16, 16);
	font-weight: 700;
	text-shadow: none;
  }
  
  .speech-text {
	color:rgb(255, 255, 255);
	font-weight: 700;
	text-shadow: 1px 1px 2px black;
  }
  
  
  .editLastMessage {
	
	position: relative;
	right: 4rem;
	top: -0.5rem;
	display: flex;
	width: 2.5rem;
	height: 2.5rem;
	font-size: 1.75rem;
	@media (max-width: 768px) {
	  height: 1.5rem; /* Ensures content fits without extra space */
	  width: 1.5rem;
	}
  }
  
  .editIcon {
	width: 2.5rem;
	height: 2.5rem;
  }
  
  .editContainer {
	display: flex;
	flex-direction: column;
	width: 100%;
	height: 100%;
	align-items: flex-start;
  }
  
  .editInput {
	min-width: 100%;
	min-height: 10rem;
	resize: vertical; /* Allow vertical resizing */
	display: block; /* Make the input a block-level element */
	background-color: rgb(56, 56, 56); /* Adjust background color */
  
	border-radius: 5px 5px 0 0; /* Rounded corners */
	color: rgb(255, 255, 255); /* Text color */
  
	padding: 5px 10px; /* Padding for spacing */
	max-height: 90vh; /* Max height to avoid overflowing */
	max-height: 90dvh; /* For dynamic viewport height */
	box-sizing: border-box; /* Ensure padding is part of the total size */
	word-wrap: break-word; /* Allow long words to wrap */
	overflow: auto; /* Add scrollbars if content overflows */
  }
  
  .editOptions {
	z-index: 10;
	left: 59.5%;
	width: 100%;
	display: flex;
	flex-direction: row;
	justify-content: flex-end;
	align-items: center;
	border-radius: 0 0 5px 5px;
	gap: 0.5rem;
	background-color: rgb(0, 0, 0); /* Adjust background color */
  
  }
  
  .cancelEdit {
	height: 100%;
	font-size: 1.5rem;
	@media (max-width: 768px) {
		font-size: 1rem;
	}
  }
  .saveEdit {
	height: 100%;
	font-size: 1.5rem;
	@media (max-width: 768px) {
		font-size: 1rem;
	}
  }
  .copyMessage {
	height: 100%;
	font-size: 1.5rem;
	@media (max-width: 768px) {
		font-size: 1rem;
	}
  }
  .deleteMessage {
	color: #ff004f;
	height: 100%;
	font-size: 1.5rem;
	@media (max-width: 768px) {
		font-size: 1rem;
	}
  }
  .moveUp {
	height: 100%;
	font-size: 1.5rem;
	@media (max-width: 768px) {
		font-size: 1rem;
	}
  }
  .moveDown {
	height: 100%;
	font-size: 1.5rem;
	@media (max-width: 768px) {
		font-size: 1rem;
	}
  }
  
  
  
  
  .chatMessage {
	position: relative;
	display: flex;
	font-size: 1.25rem;
	width: 100%;
	min-height: 5vh; /* Same as the minimum height for consistency */
	margin-bottom: 1rem;
	align-items: flex-start; /* Align content to top-left */
	gap: 1rem; /* Adds space between icon and content */
	
  }
  
  .chatIcon {
	width: 8rem;
	height: 8rem;
	border-radius: 50%;
	@media (max-width: 430px) {
		width: 2.5rem;
		height: 2.5rem;
	}
  }
  
  .chatMessageContent {
	
	left: 10rem;
	background-color: rgb(227, 132, 132);
	font-weight: 700;
	text-shadow: 1px 1px 2px black;
	width: 75%;
	border-radius: 1rem;
	padding: 1rem;
	min-height: 5vh;
	display: flex;
	flex-direction: column;
	justify-content: center;
	word-wrap: break-word;
	overflow-wrap: break-word;
	box-sizing: border-box;
	@media (max-width: 430px) {
		font-size: 0.85rem !important;
	}
  }
  
  .chatBar {
	background-color: #640726;
	width: 100dvw;
	min-height: 6rem;
	display: flex !important;
	position: absolute;
	bottom: 0rem;
	align-items: center;
	justify-content: space-around;
	box-shadow: 0 -1px 14px rgba(0, 0, 0, 0.669);
	color: rgba(0, 0, 0, 0.607);
  
  }

  .userContainer{
	display: flex;
	justify-content: space-evenly;
	width: 80dvw;
	margin-left: 10dvw;
  }
  
  .userInput {
	background-color: #ffb3c1;
	width: 65dvw;
	border-radius: 1rem;
	padding: 1rem;
	resize: vertical;

  }

  .sendButton {
	@media (max-width: 768px) {

	}
  }
  
  .sendIcon {
	  width: 4rem;
	  height: 4rem;
  }
  
  
  .previousResponse {
	font-size: 2rem;
  
  }
  
  .nextResponse {
	font-size: 2rem;
  }