/* Modal.css */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(17, 3, 3, 0.886);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  padding: 1.5rem;
  border-radius: 8px;
  min-width: 20dvw;
  max-width: 80dvw;
  position: relative;
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 0.3s ease, transform 0.3s ease;
  @media (max-width: 786px) {
    min-width: 100dvw;
    padding: 0;
    border: 1px solid white;
    padding: 0rem;
    border-radius: 0;
  }
}

.modal-content.activeModal {
  opacity: 1;
  transform: scale(1);
}

.modal-close {
  position: absolute;
  width: 3rem;
  top: 0;
  right: 1rem;
  background: none;
  border: none;
  cursor: pointer;
  border-radius: 25rem;
  font-weight: 800;
  font-size: 2rem;
  color: white;
  border: 2px solid fuchsia;
}

.token-title {
  font-size: 1rem;
}

.tokenPurchaseOptions {
	background-color: #450410;
	display: flex;
	justify-content: space-between;
	margin-top: 1rem;
	flex-direction: column;
	text-align: center;
	gap: 1.5rem;
	padding-bottom: 4rem;
	padding-left: 1rem;
	padding-right: 1rem;
	border-radius: 2rem;
	height: 27rem;
	animation: goldPinkGlow 1s ease-in-out infinite;
  }
  
  @keyframes goldPinkGlow {
	0% {
	  box-shadow: 0 0 8px 4px #ffd700, 0 0 12px 8px #ff69b4, 0 0 16px 12px #ffd700;
	}
	50% {
	  box-shadow: 0 0 12px 6px #ff69b4, 0 0 16px 10px #ffd700, 0 0 20px 14px #ff69b4;
	}
	100% {
	  box-shadow: 0 0 8px 4px #ffd700, 0 0 12px 8px #ff69b4, 0 0 16px 12px #ffd700;
	}
  }

.purchaseItem {
  border: 2px solid #c64545;
