.aiModel {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.aiModelButtons {
  display: flex;
  gap: 1rem;
}
.aiModelButtons {
  display: flex;
  gap: 10px;
  justify-content: center;
  margin: 20px 0;
  width: 100%;
  border: 1px solid white;
  @media (max-width: 786px) {
    gap: 1rem
  }
}

.aiModelButtons button {
  all: unset;
  height: 2rem;
  display: inline-block;
  padding: 12px 24px;
  border-radius: 12px;
  font-size: 16px;
  font-weight: bold;
  text-align: center;
  cursor: pointer;
  overflow: hidden;
  color: rgb(0, 0, 0);
  transition: all 0.4s ease;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  @media (max-width: 786px) {
    text-align: center;
    font-size: 0.65rem;
    padding: 0 1rem;
  }
}

.aiModelButtons button::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: inherit;
  z-index: -1;

  backdrop-filter: blur(8px);
  transition: all 0.4s ease;
}

.aiModelButtons button:hover {
  transform: translateY(-3px) scale(1.1);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.4);
}

.aiModelButtons button:nth-child(1) {
  background: linear-gradient(135deg, #ff0000, #ff7373);
  box-shadow: 0 0 15px rgba(255, 0, 0, 0.5);
}

.aiModelButtons button:nth-child(2) {
  background: linear-gradient(135deg, #ffd700, #fff089);
  box-shadow: 0 0 15px rgba(255, 215, 0, 0.5);
}

.aiModelButtons button:nth-child(3) {
  background: linear-gradient(135deg, #800080, #d884d8);
  box-shadow: 0 0 15px rgba(128, 0, 128, 0.5);
}

.aiModelButtons button:nth-child(4) {
  background: linear-gradient(135deg, #008000, #7fff7f);
  box-shadow: 0 0 15px rgba(0, 128, 0, 0.5);
}



/* Dynamic Glow Effects for Active Buttons */
.aiModelButtons button[data-model="velvet"].active {
  box-shadow: 0 0 25px 10px rgba(255, 0, 0, 0.8);
